
<template>
   <div>
      <b-table striped hover :items="items" :fields="fields"></b-table>
        <!--  -->
 <div class="container" id="s">
      <div class="row">
        <div class="col-md-4 mx-auto">
          <b-form @submit="onSubmit" @reset="onReset" v-if="show" >
            <b-form-group id="input-group-1" label="Korisnicko ime:" label-for="input-1">
              <b-form-input
                id="input-1"
                v-model="form.username"
                required
                placeholder="Enter username"
              ></b-form-input>
            </b-form-group>

            <b-form-group id="input-group-2" label="Lozinka:" label-for="input-2">
              <b-form-input
              type="password"
               id="input-2"
               v-model="form.password" 
               required placeholder="Enter password">
               </b-form-input>
               
            </b-form-group>
                        <b-form-group id="input-group-3" label="Potvrdite lozinku:" label-for="input-3">
              <b-form-input
              type="password"
               id="input-3"
               v-model="form.password2" 
               required placeholder="Enter password again">
               </b-form-input>
               
            </b-form-group>
            <b-button type="submit" variant="primary">Submit</b-button>
            <b-button type="reset" variant="danger">Reset</b-button>
          </b-form>
         
        </div>
      </div>
    </div>
        <!--  -->
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'User',
  props: ['username'],

  data() {
      return {
        show:false,
             form: {
        username: "",
        password: "",
        password2: "",
      },
        fields: [
          {
            key: 'username',
            sortable: false,
            
          },
          {
            key: 'password',
            sortable: false
          },
          {
            key: 'datumRegistracije',
            sortable: false,
            formatter: 
            value => {
              //  return new Date(value)
             return new Date(value).toLocaleDateString();

            }
            

          }, 
          {
            key: 'uloga',
            sortable: false
          },
               
        ],
        items: [],
}},
methods:{
    onSubmit(evt) {
      evt.preventDefault();
      if(this.form.password ===this.form.password2){
      // alert(JSON.stringify(this.form));


      // axios.post(`http://172.16.170.175:8081/SF-15-2018-OWP/RegisterServlet`,
      // {
      //   username:"h",        
      //   password:"h",
      // })

const params = new URLSearchParams();
params.append('username', this.form.username);
params.append('password',this.form.password );
params.append('staro',this.items[0].username)
axios({
  method: 'post',
  url: 'http://localhost:8081/SF-15-2018-OWP/EditUserServlet',
  data: params
})
  .then(function (response) {
    console.log(response);
    //     alert(JSON.stringify(response))
    if(response.data.status=="failure"){
      alert("Korisnicko ime je zauzeto")
    }
    if(response.data.status=="success"){
      alert("Uspeno ste se updejtovali")
      // rutirati na login ... !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!


    }

  })
  .catch(function (error) {
    console.log(error);
    alert(error)
  })






      

      }
      else{
          alert("Lozinke se ne poklapaju")
          this.form.password = "";
          this.form.password2 = "";
      }
    },

        onReset(evt) {
      evt.preventDefault();
      // Reset our form values
      this.form.username = "";
      this.form.password = "";
      this.form.password2 = "";

      // Trick to reset/clear native browser form validation state
      this.show = false;
      this.$nextTick(() => {
        this.show = true;
      });
    }
},

mounted (){
axios.get(`http://localhost:8081/SF-15-2018-OWP/KorisnikServlet?username=${this.username}`)
// .then(res =>  this.items.push(res.data))
.then(res => {
   this.items.push(res.data)
   if(this.$store.state.user.username!=null){
     this.show=true;
   }
   else{
     this.show=false;
   }
})

.catch(err => console.log(err))
 }
}
</script>